import React, { Component } from 'react';
import './Content.scss'
import {Link} from "react-router-dom";

export default class Content extends Component {
	render() {
		return (
				<div className='section-home__content'>
					<div className='section-home__content-title'>
						<h1 className="h1 glitch" data-text="Hello">Hello</h1>
						<h2 className="h2 glitch" data-text="My name is Alex">My name is Alex</h2>
						<h2 className="h2 glitch" data-text="I'm a Front End">I'm a Front End</h2>
						<h2 className="h2 glitch" data-text="Web Developer">Web Developer</h2>
					</div>
					<Link to='/About' className="section-home__content-link">
						<div className="link-circle">
							<span className="icon arrow"></span>
						</div>
						<p className="link-text">About me</p>
					</Link>
				</div>
		);
	}
}